@import '../variables.scss';

.raise-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: -42px;
  right: 24px;
  height: 42px;
  width: 42px;
  color: $color-name;
  font-size: $size-small;
  backdrop-filter: blur(12px);
  background-color: $color-element;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  z-index: 2;
  transition: 0.1s;

  &:active {
    filter: brightness(40%);
    transition: 0.05s;
  }

  &.shown {
    bottom: 24px;
    transition: 0.2s;
  }
}