@import '../variables.scss';

.filters-container {
  overflow: hidden;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 12px 12px;
  padding: 12px;
  position: relative;
  height: auto;
  width: 100%;
  background-color: $color-element;
  backdrop-filter: blur(6px);
  border: 1px solid $color-element;
  border-radius: 12px;
}

.filter {
  margin-bottom: -12px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  gap: 6px 0;
  position: relative;
  height: 0;
  width: 100%;
  max-width: 380px;
  transform: scaleY(0);
  opacity: 0;
  transform-origin: top;
  transition: 0s;

  &.shown {
    margin-bottom: 0;
    height: auto;
    transform: scaleY(1);
    opacity: 1;
    transition: 0.1s;
  }
}

.filter-name {
  position: relative;
  text-align: left;
  color: $color-name;
  font-size: $size-description;
  font-weight: $weight-bold;
  font-family: $family-primary;
}

.filter-select {
  box-sizing: border-box;
  padding: 6px 12px;
  position: relative;
  height: auto;
  width: 100%;
  border: none;
  border-radius: 12px;
}

.filter-pricepool-value-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px 6px;
  position: relative;
  height: auto;
  width: 100%;
  background-color: transparent;
}

.filter-pricepool-value-minimum {
  color: $color-description;
  font-size: $size-tiny;
  font-weight: $weight-thin;
  font-family: $family-secondary;
  text-align: left;
}

.filter-pricepool-value-maximum {
  color: $color-description;
  font-size: $size-tiny;
  font-weight: $weight-thin;
  font-family: $family-secondary;
  text-align: right;
}

.filter-pricepool-container {
  overflow: hidden;
  box-sizing: border-box;
  position: relative;
  height: 18px;
  width: 100%;
}

.filter-pricepool-field-container {
  overflow: hidden;
  position: relative;
  height: 18px;
  width: 100%;
  background-color: $color-element;
  border: none;
  border-radius: 12px;
}

.filter-pricepool-field-active {
  position: relative;
  height: 100%;
  width: 100%;
  background-color: $color-additional;
  border: none;
  border-radius: 12px;
}

.filter-pricepool-control-minimum {
  box-sizing: border-box;
  padding: 3px;
  position: absolute;
  top: 50%;
  left: 0;
  height: 18px;
  width: 18px;
  transform: translateY(-50%);
  background-color: transparent;
  border: none;
  border-radius: 50%;

  & div {
    position: relative;
    height: 100%;
    width: 100%;
    background-color: $color-primary;
    border: none;
    border-radius: 50%;
  }
}

.filter-pricepool-control-maximum {
  box-sizing: border-box;
  padding: 3px;
  position: absolute;
  top: 50%;
  right: 0;
  height: 18px;
  width: 18px;
  transform: translateY(-50%);
  background-color: transparent;
  border: none;
  border-radius: 50%;

  & div {
    position: relative;
    height: 100%;
    width: 100%;
    background-color: $color-primary;
    border: none;
    border-radius: 50%;
  }
}

.filter-pricepool-inputs-container {
  margin-top: 6px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px 6px;
  position: relative;
  height: auto;
  width: 100%;
}

.filter-pricepool-input {
  box-sizing: border-box;
  padding: 6px 12px;
  position: relative;
  height: auto;
  width: 100%;
  text-align: center;
}

.filters-reset {
  box-sizing: border-box;
  position: relative;
  height: auto;
  width: 100%;
  max-width: 380px;
  background-color: $color-element;
}

.filters-apply {
  box-sizing: border-box;
  position: relative;
  height: auto;
  width: 100%;
  max-width: 380px;
  background-color: $color-additional;
}

.applieds-container {
  margin-bottom: -36px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: start;
  gap: 6px 6px;
  padding: 12px;
  position: sticky;
  top: 12px;
  height: 0;
  width: 100%;
  background-color: $color-element;
  backdrop-filter: blur(6px);
  border: 1px solid $color-element;
  border-radius: 12px;
  transform: scaleY(0);
  transform-origin: top;
  z-index: 2;
  transition: 0s;

  &.shown {
    margin-bottom: 0;
    height: auto;
    transform: scaleY(1);
    opacity: 1;
    transition: 0.1s;
  }
}

.applied {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 6px;
  padding: 3px 12px;
  position: relative;
  height: auto;
  width: auto;
  background-color: $color-main;
  border: none;
  border-radius: 12px;
}

.applied-value {
  text-align: center;
  color: $color-name;
  font-size: $size-small;
  font-weight: $weight-bold;
  font-family: $family-primary;
}

.products-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: start;
  align-items: center;
  gap: 12px 12px;
  justify-content: center;
  align-items: start;
  position: relative;
  height: auto;
  width: 100%;
  background-color: transparent;
  border: none;
  border-radius: 0;
  transform: scaleY(0);
  transform-origin: top;
  opacity: 0;
  transition: 0s;

  &.shown {
    margin-bottom: 0;
    height: auto;
    transform: scaleY(1);
    transform-origin: top;
    opacity: 1;
    transition: 0.1s;
  }
}

.product {
  overflow: hidden;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  gap: 12px 0;
  padding: 12px;
  position: relative;
  height: auto;
  width: 100%;
  max-width: 380px;
  text-align: left;
  background-color: $color-element;
  backdrop-filter: blur(6px);
  border: 1px solid $color-element;
  border-radius: 12px;
}

.product-type {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: start;
  align-items: center;
  gap: 12px 6px;
  margin-bottom: 6px;
  position: relative;
  height: auto;
  width: 100%;
}

.product-type-icon {
  box-sizing: border-box;
  padding: 6px;
  position: relative;
  height: 26px;
  width: 26px;
  text-align: center;
  color: $color-name;
  font-size: $size-small;
  background-color: $color-element;
  border: none;
  border-radius: 6px;
}

.product-type-name {
  text-align: left;
  text-transform: uppercase;
  color: $color-name;
  font-size: $size-subtitle;
  font-weight: $weight-bold;
  font-family: $family-secondary;
}

.product-sort-description-title {
  box-sizing: border-box;
  display: flex;
  flex-direction: wrap;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px 6px;
  padding: 12px 0px;
  position: relative;
  height: auto;
  width: 100%;
  color: $color-title;
  font-size: $size-description;
  font-weight: $weight-bold;
  font-family: $family-primary;
  border-top: 1px solid $color-element;
  border-radius: 0;
  cursor: pointer;
}

.product-sort-description-title i {
  transform: scaleY(1);
  transition: 0.1s;
}

.product-sort-description-title i.shown {
  transform: scaleY(-1);
  transition: 0.1s;
}

.product-sort-description-container {
  margin-bottom: -12px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  gap: 12px 0;
  position: relative;
  height: 0;
  width: 100%;
  color: $color-description;
  font-size: $size-small;
  font-weight: $weight-primary;
  font-family: $family-primary;
  transform: scaleY(0);
  transform-origin: top;
  transition: 0s;
  opacity: 0;
  user-select: text;
}

.product-sort-description-container.shown {
  margin-bottom: 0;
  height: auto;
  transform: scaleY(100%);
  transition: 0.1s;
  opacity: 1;
}

.product-sort-description {
  position: relative;
  height: auto;
  width: 100%;
  color: $color-description;
  font-size: $size-small;
  font-weight: $weight-primary;
  font-family: $family-primary;
}

.product-sort-description-actions-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: start;
  position: relative;
  height: auto;
  width: 100%;
  background-color: transparent;
  border: none;
  border-radius: 0;
}

.product-sort-description-action {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px 6px;
  padding: 6px 12px;
  position: relative;
  height: auto;
  width: 100%;
  background-color: $color-element;
  border: none;
  border-radius: 6px;
}

.product-characteristics-title {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px 6px;
  padding: 12px 0px;
  position: relative;
  height: auto;
  width: 100%;
  color: $color-title;
  font-size: $size-description;
  font-weight: $weight-bold;
  font-family: $family-primary;
  border-top: 1px solid $color-element;
  border-radius: 0;
  cursor: pointer;
}

.product-characteristics-title i {
  transform: scaleY(1);
  transition: 0.1s;
}

.product-characteristics-title i.shown {
  transform: scaleY(-1);
  transition: 0.1s;
}

.product-characteristics-container {
  margin-bottom: -12px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: start;
  align-items: center;
  gap: 12px 6px;
  position: relative;
  height: 0;
  width: 100%;
  transform: scaleY(0);
  transform-origin: top;
  transition: 0s;
  opacity: 0;
}

.product-characteristics-container.shown {
  margin-bottom: 0;
  height: auto;
  transform: scaleY(100%);
  transition: 0.1s;
  opacity: 1;
}

.product-characteristic {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px 6px;
  padding: 6px 12px;
  position: relative;
  height: auto;
  width: 100%;
  color: $color-description;
  font-size: $size-small;
  font-weight: $weight-primary;
  font-family: $family-primary;
  background-color: $color-element;
  border: none;
  border-radius: 6px;
}

.product-characteristic.small {
  width: calc(100% / 2 - 3px);
}

.product-characteristic-name {
  color: $color-description;
  font-size: $size-tiny;
  font-weight: $weight-primary;
  font-family: $family-primary;
}

.product-characteristic-value {
  color: $color-description;
  font-size: $size-tiny;
  font-weight: $weight-bold;
  font-family: $family-primary;
}

.product-cost-container {
  margin-top: 12px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 12px 6px;
  position: relative;
  height: auto;
  width: 100%;
}

.product-cost-weight-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: start;
  align-items: center;
  gap: 0 6px;
  position: relative;
  height: auto;
  width: auto;
}

.product-cost-weight-remove {
  box-sizing: border-box;
  padding: 6px;
  position: relative;
  height: 26px;
  width: 26px;
  text-align: center;
  color: $color-name;
  font-size: $size-small;
  font-weight: $weight-primary;
  font-family: $family-primary;
  background-color: $color-element;
  border: none;
  border-radius: 6px;
  line-height: 1;
  cursor: pointer;

  &:active {
    filter: brightness(40%);
    transition: 0.025s;
  }

  &.disabled {
    background-color: rgba(0, 0, 0, 0.15);
    filter: brightness(40%);
    cursor: not-allowed;
    pointer-events: none;
    transition: 0.1s;
  }
}

.product-cost-weight-value {
  padding: 6px 12px;
  position: relative;
  height: auto;
  width: 52px;
  text-align: center;
  color: $color-description;
  font-size: $size-small;
  font-weight: $weight-primary;
  font-family: $family-additional;
  border: none;
  border-radius: 6px;
}

.product-cost-weight-add {
  box-sizing: border-box;
  padding: 6px;
  position: relative;
  height: 26px;
  width: 26px;
  text-align: center;
  color: $color-name;
  font-size: $size-small;
  font-weight: $weight-primary;
  font-family: $family-primary;
  background-color: $color-element;
  border: none;
  border-radius: 6px;
  line-height: 1;
  cursor: pointer;

  &:active {
    filter: brightness(40%);
    transition: 0.025s;
  }

  &.disabled {
    background-color: rgba(0, 0, 0, 0.15);
    filter: brightness(40%);
    cursor: not-allowed;
    pointer-events: none;
    transition: 0.1s;
  }
}

.product-cost {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: start;
  align-items: center;
  gap: 0 6px;
  padding: 6px 12px;
  position: relative;
  height: 26px;
  width: auto;
  color: $color-name;
  font-size: $size-description;
  font-weight: $weight-bold;
  font-family: $family-additional;
  background-color: $color-element;
  border: none;
  border-radius: 6px;
}

.product-view {
  position: relative;
  height: auto;
  width: 100%;
  background-color: $color-additional;
}

.products-empty-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 12px 6px;
  position: relative;
  height: 0;
  width: 100%;
  background-color: transparent;
  border: none;
  border-radius: 0;
  transform: scaleY(0);
  transform-origin: top;
  opacity: 0;
  transition: 0s;

  &.shown {
    margin-bottom: 0;
    height: auto;
    transform: scaleY(1);
    transform-origin: top;
    opacity: 1;
    transition: 0.1s;
  }
}

.products-empty-icon {
  position: relative;
  text-align: center;
  color: $color-name;
  font-size: $size-title;
  font-weight: $weight-primary;
  font-family: $family-primary;
}

.products-empty-text {
  position: relative;
  text-align: center;
  color: $color-description;
  font-size: $size-description;
  font-weight: $weight-primary;
  font-family: $family-primary;
}