@import '../variables.scss';

.cart-container {
  margin-bottom: -36px;
  overflow: hidden;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  gap: 12px 12px;
  padding: 12px;
  position: sticky;
  height: 0;
  width: 100%;
  background-color: $color-element;
  backdrop-filter: blur(6px);
  border: 1px solid $color-element;
  border-radius: 12px;
  transform: scaleY(0);
  transform-origin: top;
  opacity: 0;
  transition: 0s;

  &.shown {
    margin-bottom: 0;
    height: auto;
    transform: scaleY(1);
    opacity: 1;
    transition: 0.1s;
  }
}

.cart-products-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  justify-content: start;
  align-items: center;
  gap: 12px 12px;
  position: relative;
  height: auto;
  width: 100%;
  max-width: 380px;
}

.cart-product {
  overflow: hidden;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 6px 0;
  padding: 12px;
  position: relative;
  height: auto;
  width: auto;
  background-color: transparent;
  border-bottom: 3px solid $color-element;
  border-radius: 0;
}

.cart-product-type-icon {
  position: relative;
  height: 42px;
  width: 42px;
  text-align: center;
  color: $color-name;
  font-size: $size-title;
  background-color: $color-element;
  border: none;
  border-radius: 6px;
  line-height: 2;
}

.cart-product-type-name {
  position: relative;
  height: auto;
  width: auto;
  text-align: left;
  text-transform: uppercase;
  color: $color-name;
  font-size: $size-subtitle;
  font-weight: $weight-bold;
  font-family: $family-secondary;
}

.cart-product-characteristics-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 6px 6px;
  position: relative;
  height: auto;
  width: auto;
}

.cart-product-characteristic {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  gap: 6px 0;
  padding: 3px 12px;
  position: relative;
  height: auto;
  width: auto;
  color: $color-name;
  font-size: $size-small;
  font-weight: $weight-bold;
  font-family: $family-primary;
  background-color: $color-main;
  border-radius: 12px;
}

.cart-product-weight {
  margin-top: 12px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 6px 0;
  padding: 6px;
  position: relative;
  height: auto;
  width: 100%;
  color: $color-name;
  font-size: $size-small;
  font-weight: $weight-bold;
  font-family: $family-secondary;
  background-color: transparent;
  border-radius: 12px;
}

.cart-product-weight-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: start;
  align-items: center;
  gap: 0 6px;
  position: relative;
  height: auto;
  width: auto;
}

.cart-product-weight-remove {
  box-sizing: border-box;
  padding: 6px;
  position: relative;
  height: 26px;
  width: 26px;
  text-align: center;
  color: $color-name;
  font-size: $size-small;
  font-weight: $weight-primary;
  font-family: $family-primary;
  background-color: $color-element;
  border: none;
  border-radius: 6px;
  line-height: 1;
  cursor: pointer;

  &:active {
    filter: brightness(40%);
    transition: 0.025s;
  }

  &.disabled {
    background-color: rgba(0, 0, 0, 0.15);
    filter: brightness(40%);
    cursor: not-allowed;
    pointer-events: none;
    transition: 0.1s;
  }
}

.cart-product-weight-value {
  padding: 6px 12px;
  position: relative;
  height: auto;
  width: 52px;
  text-align: center;
  color: $color-description;
  font-size: $size-small;
  font-weight: $weight-primary;
  font-family: $family-additional;
  border: none;
  border-radius: 6px;
}

.cart-product-weight-add {
  box-sizing: border-box;
  padding: 6px;
  position: relative;
  height: 26px;
  width: 26px;
  text-align: center;
  color: $color-name;
  font-size: $size-small;
  font-weight: $weight-primary;
  font-family: $family-primary;
  background-color: $color-element;
  border: none;
  border-radius: 6px;
  line-height: 1;
  cursor: pointer;

  &:active {
    filter: brightness(40%);
    transition: 0.025s;
  }

  &.disabled {
    background-color: rgba(0, 0, 0, 0.15);
    filter: brightness(40%);
    cursor: not-allowed;
    pointer-events: none;
    transition: 0.1s;
  }
}

.cart-product-cost {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 6px 0;
  position: relative;
  height: 26px;
  width: auto;
  color: $color-name;
  font-size: $size-small;
  font-weight: $weight-bold;
  font-family: $family-secondary;
}

.cart-product-remove {
  box-sizing: border-box;
  padding: 6px;
  position: relative;
  height: 26px;
  width: 26px;
  text-align: center;
  color: $color-name;
  font-size: $size-small;
  font-weight: $weight-primary;
  font-family: $family-primary;
  line-height: 1;
  background-color: $color-element;
  border: none;
  border-radius: 6px;
  cursor: pointer;

  &:active {
    filter: brightness(40%);
    transition: 0.025s;
  }
}

.cart-promocode-container {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  gap: 6px 0;
  position: relative;
  height: auto;
  width: 100%;
  max-width: 380px;
}

.cart-promocode-title {
  position: relative;
  text-align: left;
  color: $color-name;
  font-size: $size-description;
  font-weight: $weight-bold;
  font-family: $family-primary;
}

.cart-promocode-search-container {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 6px 0;
  padding: 6px;
  position: relative;
  height: auto;
  width: 100%;
  background-color: $color-element;
  border: none;
  border-radius: 12px;
}

.cart-promocode-field {
  position: relative;
  height: auto;
  width: 100%;
  background-color: transparent;
  text-transform: uppercase;
  transform: scaleY(1);
  transform-origin: top;
  opacity: 1;
  transition: 0.3s;

  &.hidden {
    transform: scaleY(0);
    opacity: 0;
    transition: 0.3s;
  }
}

.cart-promocode-results-container {
  margin-top: -6px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 6px 0;
  padding: 0;
  position: relative;
  height: 0;
  width: 100%;
  background-color: transparent;
  border: none;
  border-radius: 0;
  transform: scaleY(0);
  transform-origin: top;
  opacity: 0;
  transition: 0s;

  &.shown {
    height: auto;
    transform: scaleY(1);
    opacity: 1;
    transition: 0.1s;
  }

  &.raised {
    margin-top: -51px;
    transition: 0.3s;
  }
}

.cart-promocode-result {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: start;
  align-items: center;
  gap: 0 6px;
  padding: 3px 6px;
  position: relative;
  height: auto;
  width: 100%;
  background-color: transparent;
  border: 3px solid $color-element;
  border-radius: 12px;
  cursor: pointer;
  transition: 0.2s ease;

  &.selected {
    border: 3px solid $color-additional;
    transition: 0.3s;
  }
}

.cart-promocode-result-radio-container {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  gap: 6px 0;
  padding: 6px;
  position: relative;
  height: auto;
  width: auto;
}

.cart-promocode-result-radio {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  gap: 6px 0;
  padding: 6px;
  position: relative;
  height: auto;
  width: auto;
  background-color: $color-element;
  border: none;
  border-radius: 50%;
  transition: 0.2s;

  &.selected {
    background-color: $color-additional;
    transition: 0.3s;
  }
}

.cart-promocode-result-info-container {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  gap: 3px 0;
  padding: 6px;
  position: relative;
  height: auto;
  width: auto;
}

.cart-promocode-result-info-code {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: start;
  align-items: center;
  gap: 6px 6px;
  position: relative;
  text-align: left;
  color: $color-name;
  font-size: $size-description;
  font-weight: $weight-bold;
  font-family: $family-secondary;
}

.cart-promocode-result-info-discount {
  box-sizing: border-box;
  padding: 1.5px 3px;
  position: relative;
  text-align: left;
  color: $color-subtitle;
  font-size: $size-tiny;
  font-weight: $weight-bold;
  font-family: $family-secondary;
  background-color: $color-element;
  border: none;
  border-radius: 3px;
}

.cart-promocode-result-info-description {
  position: relative;
  text-align: left;
  color: $color-subtitle;
  font-size: $size-tiny;
  font-weight: $weight-bold;
  font-family: $family-secondary;
}

.cart-cost-container {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 6px 0;
  padding: 6px 12px;
  position: relative;
  height: auto;
  width: 100%;
  max-width: 380px;
  background-color: $color-element;
  border: none;
  border-radius: 12px;
}

.cart-cost-name {
  position: relative;
  height: auto;
  width: auto;
  text-align: left;
  color: $color-name;
  font-size: $size-small;
  font-weight: $weight-bold;
  font-family: $family-primary;
}

.cart-cost-sum-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: start;
  align-items: baseline;
  gap: 0 6px;
  position: relative;
  height: auto;
  width: auto;
}

.cart-cost-sum-discounted {
  position: relative;
  height: auto;
  width: auto;
  text-decoration: none;
  color: $color-name;
  font-size: $size-subtitle;
  font-weight: $weight-bold;
  font-family: $family-secondary;
  transform: scaleX(1);
  transform-origin: right;
  opacity: 0;
  transition: 0s;

  &.selected {
    transform: scaleX(1);
    opacity: 1;
    transition: 0.1s;
  }
}

.cart-cost-sum-clear {
  position: relative;
  height: auto;
  width: auto;
  text-decoration: none;
  color: $color-name;
  font-size: $size-subtitle;
  font-weight: $weight-bold;
  font-family: $family-secondary;

  &.secondary {
    text-decoration: line-through;
    color: $color-description;
    font-size: $size-tiny;
    font-weight: $weight-bold;
    font-family: $family-secondary;
  }
}

.cart-actions-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 12px 12px;
  position: relative;
  height: auto;
  width: 100%;
  max-width: 380px;
}

.cart-action-apply {
  position: relative;
  height: auto;
  width: 100%;
  background-color: $color-additional;
}
