@import 'variables.scss';

body {
  margin: 0;
  padding: 0;
  background-color: $color-main;
  user-select: none;
}

a {
  text-decoration: none;
}

input {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px 6px;
  padding: 12px;
  color: $color-name;
  font-size: $size-description;
  font-weight: $weight-bold;
  font-family: $family-secondary;
  background-color: $color-element;
  outline: none;
  border: none;
  border-radius: 12px;
}

select {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px 6px;
  padding: 12px;
  color: $color-subtitle;
  font-size: $size-description;
  font-weight: $weight-primary;
  font-family: $family-secondary;
  background-color: $color-element;
  outline: none;
  border: none;
  border-radius: 12px;
}

option {
  color: $color-name;
  font-size: $size-description;
  font-weight: $weight-primary;
  font-family: $family-primary;
  background-color: $color-main;
}

.button {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px 6px;
  padding: 12px;
  color: $color-name;
  font-size: $size-description;
  font-weight: $weight-bold;
  font-family: $family-primary;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: 0.1s;

  &:active {
    transform: scale(95%);
    filter: brightness(60%);
    transition: 0.05s ease;
  }
}

.text-colored {
  color: white;
}

#root {
  overflow-x: hidden;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  position: relative;
  height: 100%;
  width: 100%;
  background-color: $color-main;
}

.section {
  overflow-x: hidden;
  overflow-y: scroll;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
  justify-content: center;
  gap: 12px 0;
  padding: 12px 12px 6px 12px;
  position: relative;
  height: 100%;
  width: 100%;
  background-color: transparent;
  backdrop-filter: brightness(100%);
}