@import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&display=swap');

$family-primary: 'Jost';
$family-secondary: 'Kanit';
$family-additional: 'Roboto Condensed';
$size-title: 22px;
$size-wide: 20px;
$size-subtitle: 18px;
$size-description: 16px;
$size-small: 14px;
$size-tiny: 12px;
$weight-wide: 900;
$weight-bold: 700;
$weight-primary: 500;
$weight-thin: 300;
$weight-tiny: 100;
$color-main: var(--color-main);
$color-primary: var(--color-primary);
$color-element: var(--color-element);
$color-additional: var(--color-additional);
$color-inverted: var(--color-inverted);
$color-name: var(--color-name);
$color-title: var(--color-title);
$color-subtitle: var(--color-subtitle);
$color-description: var(--color-description);
$color-link: var(--color-link);

